.page-indicator {
	height: auto;
	width: auto;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.page-indicator > * {
	margin: 4px;
}

.page-indicator-dot-selected {
	border-radius: 50%;
	height: 16px;
	width: 16px;
	background-color: red;
}

.page-indicator-dot-unselected {
	border-radius: 50%;
	height: 16px;
	width: 16px;
	background-color: lightcoral;
}
